.workflow-widget .btn svg {
  width: 20px;
}

.workflow-widget__item {
  font-size: 1.05rem;
  padding-left: 14px;
  padding-right: 14px;
  transition: color 150ms, background-color 150ms;
}

.workflow-widget__item.workflow-widget__item--active {
  color: #0079BF;
}

.workflow-widget__item:hover,
.workflow-widget__item:focus,
.workflow-widget__item:active {
  background-color: #0079BF;
}

.workflow-widget__item.workflow-widget__item--link {
  align-items: center;
  display: flex;
}

.workflow-widget__item.workflow-widget__item--link svg {
  margin-right: 4px;
}

.workflow-widget__item.workflow-widget__item--link:hover {
  background-color: #f1f3f6;
  color: #566b8d;
}




